/* Global */

html {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}

body {
    margin: 0;
    // display: flex;// flex-direction: column;
}

#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

main {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;

    &>*:not(:first-child):not(:nth-child(2)) {
        margin-top: 5rem;
    }
}

section {
    // margin-bottom: 4rem;
    width: 100%;
    height: 100%;
    max-width: 1024px;
    margin: 0 auto;
    padding: 0 4rem;
}

@media only screen and (max-width: 900px) {

    section {
        padding: 0 2rem;
    }

}